  }
  .shenpi {
    width: 100%;
    height: 0.9rem;
    line-height: 0.9rem;
    text-align: center;
    background: #f1f1f1;
    color: #999999;
    font-size: 0.3rem;
    position: relative;
    img {
      width: 110px;
      height: 3px;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
    }
  }
