          display: flex;
          align-items: center;
          color: #3674b2;
          img {
            width: 0.3rem;
            height: 0.4rem;
            margin-right: 0.2rem;
          }
        }
        .time {
          color: #c6c6c6;
          font-size: 0.28rem;
        }
      }
      .bottom {
        border-top: 1px solid #cacaca;
        padding: 0.3rem 0.5rem;
        font-size: 0.28rem;
        > li {
          line-height: 0.64rem;
          display: flex;
          justify-content: space-between;
          span {
            color: #999999;
          }
          .data {
            color: #333333;
          }
          .data.no {
            color: #f35535;
          }
        }
        > li.remark {
          display:block;
          span{
            display:block;
            line-height:0.5rem;
                  white-space: normal;
                  word-break: break-all;
          }
        }
      }
    }
    > li:first-child {
      margin-top: 0;
    }
  }
}
</style>



